<template>
  <div v-if="dataRetrieved">
    <h1>Entry Details</h1>
  <h2>Reference Number: {{ this.entryDetails?.entry_number }}</h2>
  <h3>Waybill Information</h3>
    <table>
      <thead>
        <tr>
          <th>Waybill Number  </th>
          <th>Waybill Date</th>
          <th>Mode Of Transport</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td contenteditable="true" v-html="entryDetails?.waybill_number" @blur="updateEntryFields('waybill_number', $event.target.innerText)"></td>
          <td contenteditable="true">
            <input type="date" v-model="formattedDate">
          </td>
          <td>
            <select v-model="entryDetails.mode_of_transport" @change="toggleOptions">
              <option v-for="option in modeOfTransportOOptions" :key="option" :value="option">{{ option }}</option>
            </select>
          </td>
        </tr>
        <tr>
          <th>Freight Type</th>
          <th>Marks & Numbers</th>
          <th>Package Type</th>
        </tr>
        <tr>
          <td>
            <select v-model="entryDetails.freight_type" @change="toggleOptions">
              <option v-for="option in freightTypeOptions" :key="option" :value="option">{{ option }}</option>
            </select>
          </td>
          <td contenteditable="true" v-html="entryDetails.marks_and_numbers" @blur="updateEntryFields('marks_and_numbers', $event.target.innerText)"></td>
          <td contenteditable="true">
            <select v-model="entryDetails.package_type" @change="toggleOptions">
              <option v-for="option in packageTypeOptions" :key="option.value" :value="option.value">{{ option.text }}</option>
            </select>
          </td>
        </tr>
        <tr>
          <th>CPC Code</th>
          <th>NPC Code</th>
          <th>Number of Packages</th>
        </tr>
        <tr>
          <td contenteditable="true">
            <select v-model="entryDetails.cpc_code" @change="toggleOptions">
              <option v-for="option in cpcCodeOptions" :key="option" :value="option">{{ option }}</option>
            </select>
          </td>
          <td contenteditable="true">
            <select v-model="entryDetails.npc_code" @change="toggleOptions">
              <option v-for="option in npcCodeOptions" :key="option" :value="option">{{ option }}</option>
            </select>
          </td>
          <td contenteditable="true" >
            <input type="number" v-model="entryDetails.package_quantity">
          </td>
        </tr>
      </tbody>
    </table>
    <!-- End of Waybill Table -->

    <!-- Entry Information Table -->

    <h3>Entry Information</h3>
    <table>
      <thead>
        <tr>
          <th>Consignee</th>
          <th>Shipper</th>
          <th>Supplier</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td contenteditable="true" v-html="entryDetails.consignee" @blur="updateEntryFields('consignee', $event.target.innerText)"></td>
          <td contenteditable="true" v-html="entryDetails.shipper" @blur="updateEntryFields('shipper', $event.target.innerText)"></td>
          <td contenteditable="true" v-html="entryDetails.supplier_name" @blur="updateEntryFields('supplier', $event.target.innerText)"></td>
        </tr>
        <tr>
          <th>Total Cargo Value</th>
          <th>Freight Charge</th>
          <th>Rate of Exchange</th>
        </tr>
        <tr>
          <td contenteditable="true" v-html="entryDetails.invoice_total" @blur="updateEntryFields('invoice_total', $event.target.innerText)"></td>
          <td contenteditable="true" v-html="entryDetails.freight_charge" @blur="updateEntryFields('freight_charge', $event.target.innerText)"></td>
          <td contenteditable="true" v-html="entryDetails.rate_of_exchange" @blur="updateEntryFields('rate_of_exchange', $event.target.innerText)"></td>
        </tr>
        <tr>
          <th>Net Weight</th>
          <th>Gross Weight</th>
          <th>Country of Origin</th>
        </tr>
        <tr>
          <td contenteditable="true" v-html="entryDetails.net_weight" @blur="updateEntryFields('net_weight', $event.target.innerText)"></td>
          <td contenteditable="true" v-html="entryDetails.gross_weight" @blur="updateEntryFields('gross_weight', $event.target.innerText)"></td>
          <td contenteditable="true" >
            <select v-model="entryDetails.country_of_origin" id="country">
              <option disabled value="">Please select one</option>
              <option v-for="country in countries" :key="country.code" :value="country.code">
                {{ country.code }} - {{ country.name }}
              </option>
            </select>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- End of Entry Information -->

    <!-- Commercial Invoices Table -->
    <div v-if="dataRetrieved">
      <h3>Commercial Invoices</h3>
      <div v-for="(data, dataIndex) in commercialInvoices" :key="dataIndex">
        <h2>Invoice Data #{{ dataIndex + 1 }}</h2>
        <h3>Header Information</h3>
        <table>
          <thead>
            <tr>
              <th>Invoice #</th>
              <th>Invoice Date</th>
              <th>Supplier Name</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'invoice_number', $event.target.innerText)">{{ data.invoice_number || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'invoice_date', $event.target.innerText)">{{ data.invoice_date || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'vendor_name', $event.target.innerText)">{{ data.supplier_name || 'N/A' }}</td>
            </tr>
          </tbody>
        </table>
        <h3>Line Details</h3>
        <table>
          <thead>
            <tr>
              <th>Product Code</th>
              <th>Description</th>
              <th>THN Code</th>
              <th>Unit Quantity</th>
              <th>Unit Price</th>
              <th>Extension Price</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, itemIndex) in data.lines" :key="itemIndex">
              <td contenteditable="true" @blur="updateInvoiceData(item, 'product_code', $event.target.innerText)">{{ item.product_code || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(item, 'description', $event.target.innerText)">{{ item.description || 'N/A' }}</td>
              <td contenteditable="true" @input="handleInput($event)" @blur="updateInvoiceData(item, 'tariff_code', $event.target.innerText)">{{ item.tariff_code || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(item, 'quantity', $event.target.innerText)" :style="naStyle(item.quantity)">{{ item.quantity || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(item,'unit_price', $event.target.innerText)" :style="naStyle(item.unit_price)">{{ item.unit_price || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(item,'extension_price', $event.target.innerText)" :style="extensionStyle(item)">{{ item.extension_price || 'N/A' }}</td>
              <td><button @click="deleteRow(data, itemIndex)">Delete</button></td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="7"><button @click="addNewLine(data)">Add New Line</button></td>
            </tr>
          </tfoot>
        </table>
        <h3>Footer Information</h3>
        <table>
          <thead>
            <tr>
              <th>Invoice Total</th>
              <th>Trucking</th>
              <th>Final Total</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'invoice_total', $event.target.innerText)" :style="naStyle(data.invoice_total)">{{ data.invoice_total || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'trucking', $event.target.innerText)" :style="naStyle(data.trucking)">{{ data.trucking || 'N/A' }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'final_total', $event.target.innerText)" :style="naStyle(data.final_total)">{{ data.final_total || 'N/A' }}</td>
            </tr>
          </tbody>
        </table>

        <h3>Calculated Check</h3>
        <table>
          <thead>
            <tr>
              <th>Total Extension Prices</th>
              <th>Number of Line Items</th>
              <th>Matches Invoice Total?</th>
            </tr>
          </thead>
          <tbody>
            <tr :style="checkInvoiceTotal(data)">
              <td>{{ calculateTotalExtensionPrice(data.lines) }}</td>
              <td>{{ data.lines.length }}</td>
              <td>{{ calculateTotalExtensionPrice(data.lines) === parseFloat(data.invoice_total).toFixed(2) ? 'Yes' : 'No' }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div>
      <table>
        <tr>
          <td><button :disabled="isGenerateCsvDisabled" @click="generateCSV">Generate CSV File</button></td>
          <td><button @click="saveData">Save Entry</button></td>
        </tr>
      </table>
    </div>
    </div>
    


  </div>
  
</template>

<script>
import axios from 'axios';
import * as SweetAlertConnector from '../composables/SweetAlertConnector';

export default {
  name: 'EntryDetails',
  props: {
    entryId: {
      type: [String, Number],
      required: true
    }
  },
  computed: {
    formattedDate: {
      get() {
        const date = new Date(this.entryDetails.waybill_date);
        return date.toISOString().split('T')[0];
      },
      set(newValue) {
        this.entryDetails.waybill_date = newValue;
      }
    },
    isGenerateCsvDisabled() {
      return this.commercialInvoices.some(invoice => {
        console.log("Tariff Check" + invoice);
        return invoice.lines.some(line => !line.tariff_code || line.tariff_code.trim() === '');
      });
    },
  },
  data() {
    return {
      entryDetails: null,
      waybill: {},
      entry: {},
      commercialInvoices: [],
      dataRetrieved: false,
      loading: false,
      error: null,
      countries: [],
      modeOfTransportOOptions: ['Air', 'Ocean'],
      freightTypeOptions: ['FCL', 'LCL', 'Bulk', 'Break Bulk'],
      cpcCodeOptions: ['4000'],
      npcCodeOptions: ['0000'],
      packageTypeOptions: [
        {value: 'CT', text: 'Carton'},
      ],
    };
  },
  mounted() {
    this.fetchAllDetails();
    this.fetchCountries();
  },
  methods: {
    updateWaybillDate(event) {
      // Update entryDetails.waybill_date when the input changes
      this.entryDetails.waybill_date = event.target.value;
    },
    async fetchCountries() {
      try {
        const response = await fetch('https://restcountries.com/v3/all');
        const data = await response.json();
        this.countries = data.map(country => ({
          name: country.name.common,
          code: country.cca2
        }));
      } catch (error) {
        console.error('Failed to fetch countries:', error);
      }
    },
    formatInvoiceData(invoice){
      return {
        invoice_id: invoice.invoice_id,
        invoice_number: invoice.invoice_number,
        invoice_date: invoice.invoice_date,
        invoice_total: invoice.invoice_total,
        sub_total: invoice.sub_total,
        supplier_name: invoice.supplier_name,
        taxed_amount: invoice.taxed_amount,
        supplier_address: invoice.supplier_address,
        purchase_order_number: invoice.purchase_order_number,
        entry_id: invoice.entry_id,
      };
    },
    formatInvoiceLinesData(invoiceLines) {
      return invoiceLines.map(line => {
        return {
          invoice_line_id: line.invoice_line_id,
          description: line.description,
          quantity: line.quantity,
          unit_price: line.unit_price,
          extension_price: line.extension_price,
          tariff_code: line.tariff_code,
          product_code: line.product_code
        };
      });
    },
    handleInput(event) {
      const value = event.target.innerText;
      // Remove any existing dots and non-numeric characters
      const sanitizedValue = value.replace(/[^0-9]/g, '');
      // Format the value as XXXX.XX.XX
      const formattedValue = this.formatValue(sanitizedValue);
      // Update the text content of the cell
      event.target.innerText = formattedValue;
      this.moveCursorToEnd(event.target);
      // Update the item data after handling the input
    },
    formatValue(value) {
      let formattedValue = '';
      // Insert dots after the first four characters and after the next two characters
      for (let i = 0; i < value.length; i++) {
        if (i === 4 || i === 6) {
          formattedValue += '.';
        }
        formattedValue += value[i];
      }
      return this.truncateString(formattedValue);
    },
    truncateString(str) {
      if (str.length > 10) {
        return str.substring(0, 10);
      }
      return str;
    },
    deleteRow(data, index) {
      data.lines.splice(index, 1);
    },
    addNewLine(data) {
      const newItem = {
        product_code: '',
        description: '',
        thn_code: '',
        quantity: '',
        unit_price: '',
        amount: ''
      };
      data.lines.push(newItem);
    },
    moveCursorToEnd(element) {
      const range = document.createRange();
      const selection = window.getSelection();
      range.selectNodeContents(element);
      range.collapse(false); // Collapse range to end
      selection.removeAllRanges();
      selection.addRange(range);
    },
    saveData() {
      console.log('Saving data...');
      this.loading = true;
      SweetAlertConnector.showLoadingAlert('Saving Data...');
      Promise.all([this.updateEntryData(), this.updateWaybillData(), this.saveInvoiceData()])
        .then(([entryResponse, waybillResponse]) => {
          // Handle the success response for both
          console.log('Entry details updated:', entryResponse.data);
          console.log('Waybill details updated:', waybillResponse.data);
          SweetAlertConnector.closeCurrentAlert();
          SweetAlertConnector.showSuccessAlert('All Data Saved successfully!');
        })
        .catch(error => {
          // Handle any errors that occur during the API call
          console.error('There was an error saving the data!', error);
          SweetAlertConnector.showErrorAlert('Failed to save Entry Data!');
        })
        .finally(() => {
          this.loading = false;
        });
    },
    naStyle(value) {
      if ([null, undefined, '', false].includes(value)) {
        return { backgroundColor: 'yellow', color: 'black' };
      }
      return {};
    },
    calculateTotalExtensionPrice(items) {
      return items.reduce((acc, item) => acc + parseFloat(item.extension_price || 0), 0).toFixed(2);
    },
    extensionStyle(item) {
      const expected = (parseFloat(item.unit_price) * parseFloat(item.quantity)).toFixed(2);
      const actual = parseFloat(item.extension_price).toFixed(2);
      if ([item.unit_price, item.quantity, item.extension_price].some(v => [null, undefined, '', false].includes(v))) {
        return { backgroundColor: 'yellow', color: 'black' };
      } else if (expected !== actual) {
        return { backgroundColor: 'red', color: 'white' };
      }
      return {};
    },
    checkInvoiceTotal(data) {
      const totalExtensions = this.calculateTotalExtensionPrice(data.lines);
      const invoiceTotal = parseFloat(data.invoice_total || 0).toFixed(2);
      return totalExtensions !== invoiceTotal ? { backgroundColor: 'red', color: 'white' } : {};
    },
    toggleOptions() {
      console.log(this.freightType)
    },
    updateEntryFields(fieldName, value) {
      console.log(fieldName, value)
      this.entryDetails[fieldName] = value;
    },

    // updateInvoiceFields(invoiceId, fieldName, value) {
    //   const invoice = this.commercialInvoices.find(invoice => invoice.invoice_id === invoiceId);
    //   invoice[fieldName] = value;
    // },
    updateInvoiceData(data, field, value) {
      data[field] = value;
      console.log(data);
    },
    async fetchAllDetails() {
      const api_url = process.env.VUE_APP_API_BASE_URL;
      console.log("Fetching details")
      this.loading = true;
      this.dataRetrieved = false;
      try {
        const entryDetailsPromise = axios.get(`${api_url}entry-details/${this.entryId}`);
        const commercialInvoicesPromise = axios.get(`${api_url}commercial-invoices-with-lines/${this.entryId}`);

        const [entryDetailsResponse, commercialInvoicesResponse] = await Promise.all([
          entryDetailsPromise,
          commercialInvoicesPromise
        ]);
        this.dataRetrieved = true;

        this.entryDetails = entryDetailsResponse.data;
        this.commercialInvoices = commercialInvoicesResponse.data;

        console.log('Entry details:', this.entryDetails);
        console.log('Commercial invoices:', this.commercialInvoices);
      } catch (error) {
        this.error = 'Failed to load entry details: ' + (error.response?.data || error.message);
      } finally {
        this.loading = false;
      }
    },
    async updateWaybillData(){
      this.waybill = {
        waybill_number: this.entryDetails.waybill_number,
        waybill_date: this.entryDetails.waybill_date,
        mode_of_transport: this.entryDetails.mode_of_transport,
        freight_type: this.entryDetails.freight_type,
        marks_and_numbers: this.entryDetails.marks_and_numbers,
        package_type: this.entryDetails.package_type,
        cpc_code: this.entryDetails.cpc_code,
        npc_code: this.entryDetails.npc_code,
        package_quantity: this.entryDetails.package_quantity,
        country_of_origin: this.entryDetails.country_of_origin,
        entry_id: this.entryDetails.entry_id,
        waybill_id: this.entryDetails.waybill_id
      }

      console.log("Saving Waybill Data: ", this.waybill);
      return axios.put(`${process.env.VUE_APP_API_BASE_URL}waybill/${this.waybill.waybill_id}`, this.waybill)
    },

    async updateEntryData(){
      this.entry = {
        mawi_invoice: this.entryDetails.mawi_invoice,
        entry_date: this.entryDetails.entry_date,
        invoice_total: this.entryDetails.invoice_total,
        supplier_name: this.entryDetails.supplier_name,
        gross_weight: this.entryDetails.gross_weight,
        net_weight: this.entryDetails.net_weight,
        entry_number: this.entryDetails.entry_number,
        freight_charge: this.entryDetails.freight_charge,
        rate_of_exchange: this.entryDetails.rate_of_exchange,
        consignee: this.entryDetails.consignee,
        shipper: this.entryDetails.shipper,
      }

      console.log(this.entryDetails);
      return axios.put(`${process.env.VUE_APP_API_BASE_URL}customs-entry/${this.entryDetails.entry_id}`, this.entryDetails)
    },

    async saveInvoiceData(){
      const updatePromises = this.commercialInvoices.map(invoice => {
        const formattedInvoiceData = this.formatInvoiceData(invoice);
        const formattedInvoiceLinesData = this.formatInvoiceLinesData(invoice.lines);

        const requestData = {
          invoiceData: formattedInvoiceData,
          invoiceLines: formattedInvoiceLinesData
        };

        // Store the promise without calling then() or catch() on it yet
        return axios.put(`${process.env.VUE_APP_API_BASE_URL}update-invoice-and-lines/${formattedInvoiceData.invoice_id}`, requestData);
      });

      return Promise.all(updatePromises)
      // .then(responses => {
      //   // All requests are successful if this block is executed.
      //   // 'responses' is an array of all the axios responses
      //   console.log('All invoices and invoice lines updated successfully:', responses);
      //   SweetAlertConnector.showSuccessAlert('All Invoice Data Saved successfully!');
      // })
      // .catch(error => {
      //   // If any request fails, this block is executed
      //   console.error('An error occurred while updating invoices and invoice lines:', error);
      //   alert('Failed to update some or all invoices and invoice lines. Check the console for more details.');
      //   SweetAlertConnector.showErrorAlert('Failed to save Invoice Data!');
      // });

    },

    checkTHNCodes() {
      // Iterate through the array
      let invoiceData = this.commercialInvoices;
      invoiceData.forEach(invoice => {
        invoice.lines.forEach(line => {
          // Check if the THN code is missing
          if (!line.tariff_code || line.tariff_code.trim() === '' || line.tariff_code === 'N/A' || line.tariff_code === undefined || line.tariff_code === null) {
            // Display an error message
            
            return false; // Return false if any item is missing a THN code
          }
        });
        });
      return true; // Return true if all items have a THN code
    },

    generateCSV() {
      if (!this.checkTHNCodes()) {
        alert('Please enter a THN code for all items before generating the CSV file');
        return;
      }

      let entryData = this.entryDetails;
      let invoiceData = this.commercialInvoices;
      // Helper function to convert a value to CSV format
      const toCSVText = (value) => (value !== undefined && value !== null ? `"${value.toString().replace(/"/g, '""')}"` : '');

      // Column titles
      const headers = [
        "reference", "waybill_number", "Product Code", "description", "commodity_code",
        "quantity", "unitcost", "total", "gross_weight", "net_weight", "country_origin_code", 
        "package_quantity", "kind_of_package", "marks_and_numbers", "customs_procedure_code", "national_procedure_code", "exchange_rate"
      ];

      // Start with the header
      let csvContent = headers.join(',') + '\n';

      console.log(entryData);

      // Process each invoice and its lines
      invoiceData.forEach(invoice => {
        for(let i = 0; i < invoice.lines.length; i++){
          const line = invoice.lines[i];
          let package_quantity = '0';
          if(i === 0){
            package_quantity = entryData.package_quantity;
          }
          const row = [
            toCSVText(entryData.entry_number),
            toCSVText(entryData.waybill_number),
            toCSVText(line.product_code),
            toCSVText(line.description),
            toCSVText(line.tariff_code.replace(/\./g, '')) + '000',
            toCSVText(line.quantity),
            toCSVText(line.unit_price),
            toCSVText(line.extension_price),
            toCSVText(entryData.gross_weight),
            toCSVText(entryData.net_weight),
            toCSVText(entryData.country_of_origin),
            toCSVText(package_quantity),
            toCSVText(entryData.package_type),
            toCSVText(entryData.marks_and_numbers),
            toCSVText(entryData.cpc_code),
            toCSVText(entryData.npc_code),
            toCSVText(entryData.rate_of_exchange)
          ];
          csvContent += row.join(',') + '\n';
        }
      });

      // Create a Blob for the CSV content
      const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
      
      // Create a link and trigger the download
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      const FileName = `GABRIEL-Export - ${entryData.entry_number}.csv`;
      link.setAttribute('href', url);
      link.setAttribute('download',FileName);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);

      SweetAlertConnector.showSuccessAlert('Successfully Generated CSV File!');
    },
  }
};
</script>

<style>
.error {
  color: red;
}
/* Add your styles here */
button {
  margin: 5px;
  cursor: pointer;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  margin-top: 60px;
}

.dropzone {
  border: 2px dashed #ccc;
  padding: 20px;
  border-radius: 5px;
  background: #fafafa;
  color: #bdbdbd;
