<template>
  <div id="app">
    <h1>New Entry</h1>
    <input type="text" v-model="referenceNumber" placeholder="Enter Reference Number" @input="validateInput"/>

    <div v-if="isValidReference">
      <div  class="dropzone"
        @dragover.prevent="dragOver"
        @dragenter.prevent="dragEnter"
        @dragleave.prevent="dragLeave"
        @drop.prevent="handleDrop">
        Drop files here or 
        <input type="file" multiple @change="handleFileUpload" style="display: none;">
        <button onclick="document.querySelector('input[type=file]').click()">Choose Files</button>
        <button @click="uploadFiles">Upload Files</button>
      </div>
      <div v-if="selectedFiles.length">
        <ul >
          <li v-for="(file, index) in selectedFiles" :key="index">{{ file.name }}</li>
        </ul>
        <h3>Waybill Information</h3>
        <table>
          <thead>
            <tr>
              <th>Waybill Number  </th>
              <th>Waybill Date</th>
              <th>Mode Of Transport</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td contenteditable="true" @blur="updateEntryFields('waybillNumber', $event.target.innerText)"></td>
              <td contenteditable="true">
                <input type="date" v-model="waybillDate">
              </td>
              <td>
                <select v-model="modeOfTransport" @change="toggleOptions">
                  <option v-for="option in modeOfTransportOOptions" :key="option" :value="option">{{ option }}</option>
                </select>
              </td>
            </tr>
            <tr>
              <th>Freight Type</th>
              <th>Marks & Numbers</th>
              <th>Package Type</th>
            </tr>
            <tr>
              <td>
                <select v-model="freightType" @change="toggleOptions">
                  <option v-for="option in freightTypeOptions" :key="option" :value="option">{{ option }}</option>
                </select>
              </td>
              <td contenteditable="true" v-html="marksAndNumbers" @blur="updateEntryFields('marksAndNumbers', $event.target.innerText)"></td>
              <td contenteditable="true">
                <select v-model="kindOfPackage" @change="toggleOptions">
                  <option v-for="option in packageTypeOptions" :key="option.value" :value="option.value">{{ option.text }}</option>
                </select>
              </td>
            </tr>
            <tr>
              <th>CPC Code</th>
              <th>NPC Code</th>
              <th>Number of Packages</th>
            </tr>
            <tr>
              <td contenteditable="true">
                <select v-model="cpcCode" @change="toggleOptions">
                  <option v-for="option in cpcCodeOptions" :key="option" :value="option">{{ option }}</option>
                </select>
              </td>
              <td contenteditable="true">
                <select v-model="npcCode" @change="toggleOptions">
                  <option v-for="option in npcCodeOptions" :key="option" :value="option">{{ option }}</option>
                </select>
              </td>
              <td contenteditable="true" >
                <input type="number" v-model="numberOfPackages">
              </td>
            </tr>
          </tbody>
        </table>
        <h3>Entry Information</h3>
        <table>
          <thead>
            <tr>
              <th>Consignee</th>
              <th>Shipper</th>
              <th>Supplier</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td contenteditable="true" @blur="updateEntryFields('consignee', $event.target.innerText)"></td>
              <td contenteditable="true" @blur="updateEntryFields('shipper', $event.target.innerText)"></td>
              <td contenteditable="true" @blur="updateEntryFields('supplier', $event.target.innerText)"></td>
            </tr>
            <tr>
              <th>Total Cargo Value</th>
              <th>Freight Charge</th>
              <th>Rate of Exchange</th>
            </tr>
            <tr>
              <td contenteditable="true" v-html="totalCargoValue" @blur="updateEntryFields('totalCargoValue', $event.target.innerText)"></td>
              <td contenteditable="true" v-html="freightCharge" @blur="updateEntryFields('freightCharge', $event.target.innerText)"></td>
              <td contenteditable="true" v-html="rateOfExchange" @blur="updateEntryFields('rateOfExchange', $event.target.innerText)"></td>
            </tr>
            <tr>
              <th>Net Weight</th>
              <th>Gross Weight</th>
              <th>Country of Origin</th>
            </tr>
            <tr>
              <td contenteditable="true" v-html="netWeight" @blur="updateEntryFields('netWeight', $event.target.innerText)"></td>
              <td contenteditable="true" v-html="grossWeight" @blur="updateEntryFields('grossWeight', $event.target.innerText)"></td>
              <td contenteditable="true" >
                <select v-model="countryOfOrigin" id="country">
                  <option disabled value="">Please select one</option>
                  <option v-for="country in countries" :key="country.code" :value="country.code">
                    {{ country.code }} - {{ country.name }}
                  </option>
                </select>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      
    </div>
    
    
    <div v-for="(data, dataIndex) in invoiceDataArray" :key="dataIndex">
      <h2>Invoice Data #{{ dataIndex + 1 }}</h2>
      <h3>Header Information</h3>
      <table>
        <thead>
          <tr>
            <th>Invoice #</th>
            <th>Invoice Date</th>
            <th>Supplier Name</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td contenteditable="true" @blur="updateData(data, 'invoice_id', $event.target.innerText)">{{ data.invoice_id || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateData(data, 'invoice_date', $event.target.innerText)">{{ data.invoice_date || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateData(data, 'vendor_name', $event.target.innerText)">{{ data.vendor_name || 'N/A' }}</td>
          </tr>
        </tbody>
      </table>

      

      <h3>Line Details</h3>
      <table>
        <thead>
          <tr>
            <th>Product Code</th>
            <th>Description</th>
            <th>THN Code</th>
            <th>Unit Quantity</th>
            <th>Unit Price</th>
            <th>Extension Price</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, itemIndex) in data.invoice_items" :key="itemIndex">
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'product_code', $event.target.innerText)">{{ item.product_code || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'description', $event.target.innerText)">{{ item.description || 'N/A' }}</td>
            <td contenteditable="true" @input="handleInput($event, itemIndex, 'thn_code')" @blur="updateItem(data, itemIndex, 'thn_code', $event.target.innerText)">{{ item.thn_code || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'quantity', $event.target.innerText)" :style="naStyle(item.quantity)">{{ item.quantity || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'unit_price', $event.target.innerText)" :style="naStyle(item.unit_price)">{{ item.unit_price || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateItem(data, itemIndex, 'amount', $event.target.innerText)" :style="extensionStyle(item)">{{ item.amount || 'N/A' }}</td>
            <td><button @click="deleteRow(data, itemIndex)">Delete</button></td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="7"><button @click="addNewLine(data)">Add New Line</button></td>
          </tr>
        </tfoot>
      </table>

      <h3>Footer Information</h3>
      <table>
        <thead>
          <tr>
            <th>Invoice Total</th>
            <th>Trucking</th>
            <th>Final Total</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td contenteditable="true" @blur="updateData(data, 'invoice_total', $event.target.innerText)" :style="naStyle(data.invoice_total)">{{ data.invoice_total || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateData(data, 'trucking', $event.target.innerText)" :style="naStyle(data.trucking)">{{ data.trucking || 'N/A' }}</td>
            <td contenteditable="true" @blur="updateData(data, 'final_total', $event.target.innerText)" :style="naStyle(data.final_total)">{{ data.final_total || 'N/A' }}</td>
          </tr>
        </tbody>
      </table>

      <h3>Calculated Check</h3>
      <table>
        <thead>
          <tr>
            <th>Total Extension Prices</th>
            <th>Number of Line Items</th>
            <th>Matches Invoice Total?</th>
          </tr>
        </thead>
        <tbody>
          <tr :style="checkInvoiceTotal(data)">
            <td>{{ calculateTotalExtensionPrice(data.invoice_items) }}</td>
            <td>{{ data.invoice_items.length }}</td>
            <td>{{ calculateTotalExtensionPrice(data.invoice_items) === parseFloat(data.invoice_total).toFixed(2) ? 'Yes' : 'No' }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-if="selectedFiles.length">
      <table>
        <tr >
          <td><button :disabled="entryButtonDisabled" @click="createInvoices(invoiceDataArray)">Save Entry</button></td>
          <td></td>
        </tr>
      </table>
    </div>
    
  </div>
</template>

<script>
import * as DatabaseConnector from '@/composables/DatabaseConnector';
import * as SweetAlertConnector from '../composables/SweetAlertConnector';

export default {
  /* eslint-disable */
  name: 'App',
  data() {
    return {
      referenceNumber: '',
      selectedFiles: [],
      invoiceDataArray: [],
      isDragOver: false,
      isValidReference: false,

      waybillNumber: '',
      waybillDate: new Date().toISOString().substr(0, 10),
      modeOfTransport: '',

      freightType: '',
      marksAndNumbers: 'As Addressed',
      countryOfOrigin: '',

      cpcCode: '4000',
      npcCode: '0000',
      kindOfPackage: 'CT',
      numberOfPackages: '',

      consignee: '',
      shipper: '',
      supplier: '',
      totalCargoValue: 0.0,
      freightCharge: 0.0,
      rateOfExchange: 1.0,
      netWeight: 0.0,
      grossWeight: 0.0,

      editableContent: '0',

      countries: [],
      modeOfTransportOOptions: ['Air', 'Ocean'],
      freightTypeOptions: ['FCL', 'LCL', 'Bulk', 'Break Bulk'],
      cpcCodeOptions: ['4000'],
      npcCodeOptions: ['0000'],
      packageTypeOptions: [
        {value: 'CT', text: 'Carton'},
      ],
      showModeofTransportDropdown: false,

    };
  },

  setup() {

    return {
    };
  },
  mounted() {
    this.fetchCountries();
    this.updateEntryFields('marksAndNumbers', 'As Addressed')
    this.calculateTotalCargoValue()
  },
  computed: {
    entryButtonDisabled(){
      return !(this.hasInvoiceNumber || this.waybillNumber.trim().length > 0 || this.consignee.trim().length > 0) 
    },
    hasInvoiceNumber(){
      for(let invoice of this.invoiceDataArray){
        if(invoice.invoice_id && invoice.invoice_id.trim().length > 0){
          console.log("Invoice Number Found")
          return true
        }
      }
      console.log("Invoice Number Not Found")
      return false;
    }
  },
  methods: {
    formatInvoiceData(){
      this.invoiceDataArray.forEach(invoice => {
        invoice.invoice_total = this.parseFloatFromString(invoice.invoice_total)
        invoice.trucking = this.parseFloatFromString(invoice.trucking)
        invoice.final_total = this.parseFloatFromString(invoice.final_total)
        invoice.invoice_items.forEach(item => {
          item.quantity = this.parseFloatFromString(item.quantity)
          item.unit_price = this.parseFloatFromString(item.unit_price)
          item.amount = this.parseFloatFromString(item.amount)
        })
      })
    },
    calculateTotalCargoValue(){
      let total = 0;
      for(let invoice of this.invoiceDataArray){
        console.log(invoice.invoice_total)
        total += this.parseFloatFromString(invoice.invoice_total)
      }
      this.totalCargoValue = total;
    },
    validateIntegerInput(event) {
      // Get the entered text
      let newText = event.target.innerText.trim();

      // Check if the entered text is a valid integer
      if (/^\d+$/.test(newText)) {
        // Update the content if it's a valid integer
        this.editableContent = newText;
      } else {
        // Revert to the previous content if it's not a valid integer
        event.target.innerText = this.editableContent;
      }
      this.moveCursorToEnd(event.target);
    },
    async fetchCountries() {
      try {
        const response = await fetch('https://restcountries.com/v3.1/all');
        const data = await response.json();
        this.countries = data.map(country => ({
          name: country.name.common,
          code: country.cca2
        }));
      } catch (error) {
        console.error('Failed to fetch countries:', error);
      }
    },
    toggleOptions() {
      console.log(this.freightType)
    },
    updateEntryFields(fieldName, value) {
      console.log(fieldName, value)
      this[fieldName] = value;
    },
    selectOption(option) {
      this.$refs.editableDiv.innerText = option;
      this.showModeofTransportDropdown = false;
    },
    async createInvoices(invoiceDataArray){
      const entryData = {
        referenceNumber: this.referenceNumber,
        waybillNumber: this.waybillNumber,
        waybillDate: this.waybillDate,
        modeOfTransport: this.modeOfTransport,
        freightType: this.freightType,
        marksAndNumbers: this.marksAndNumbers,
        countryOfOrigin: this.countryOfOrigin,
        cpcCode: this.cpcCode,
        npcCode: this.npcCode,
        kindOfPackage: this.kindOfPackage,
        numberOfPackages: this.numberOfPackages,
        consignee: this.consignee,
        shipper: this.shipper,
        supplier: this.supplier,
        totalCargoValue: this.totalCargoValue,
        freightCharge: this.freightCharge,
        rateOfExchange: this.rateOfExchange,
        netWeight: this.netWeight,
        grossWeight: this.grossWeight,
        invoiceList: invoiceDataArray
      };
      console.log(entryData)
      try {
        SweetAlertConnector.showLoadingAlert('Saving your Entry...');
        await DatabaseConnector.createCommercialInvoice(entryData);
        SweetAlertConnector.closeCurrentAlert();
        SweetAlertConnector.showSuccessAlert('Entry created successfully');
        // Optionally reset the form or redirect the user
      } catch (error) {
        console.error('Failed to create the invoice:', error);
        SweetAlertConnector.showErrorAlert('Failed to create the Entry. Please try again.');
      }
    },
    handleInput(event, itemIndex, field) {
      const value = event.target.innerText;
      // Remove any existing dots and non-numeric characters
      const sanitizedValue = value.replace(/[^0-9]/g, '');
      // Format the value as XXXX.XX.XX
      const formattedValue = this.formatValue(sanitizedValue);
      // Update the text content of the cell
      event.target.innerText = formattedValue;
      this.moveCursorToEnd(event.target);
      // Update the item data after handling the input
    },
    formatValue(value) {
      let formattedValue = '';
      // Insert dots after the first four characters and after the next two characters
      for (let i = 0; i < value.length; i++) {
        if (i === 4 || i === 6) {
          formattedValue += '.';
        }
        formattedValue += value[i];
      }
      return this.truncateString(formattedValue);
    },
    truncateString(str) {
      if (str.length > 10) {
        return str.substring(0, 10);
      }
      return str;
    },
    moveCursorToEnd(element) {
      const range = document.createRange();
      const selection = window.getSelection();
      range.selectNodeContents(element);
      range.collapse(false); // Collapse range to end
      selection.removeAllRanges();
      selection.addRange(range);
    },
    validateInput() {
      this.isValidReference = this.referenceNumber.trim().length > 0;
    },
    handleFileUpload(event) {
      if (!this.isValidReference) return;
      this.selectedFiles = Array.from(event.target.files);
    },
    dragOver(event) {
      if (!this.isValidReference) return;
      this.isDragOver = true;
    },
    dragEnter(event) {
      if (!this.isValidReference) return;
      this.isDragOver = true;
    },
    dragLeave(event) {
      if (!this.isValidReference) return;
      this.isDragOver = false;
    },
    uploadFiles() {
      if (!this.isValidReference || this.selectedFiles.length === 0) {
        alert("Please enter a valid reference number and select files before uploading.");
        return;
      }
      const formData = new FormData();
      formData.append('reference', this.referenceNumber);
      this.selectedFiles.forEach(file => {
        formData.append('files', file);
      });
      SweetAlertConnector.showLoadingAlert('Reading your invoice...');

      fetch('https://gabrielle-flask.azurewebsites.net/read_vendor_invoice', {
        method: 'POST',
        body: formData
      })
      .then(response => response.json())
      .then(data => {
          this.invoiceDataArray = data;
          this.formatInvoiceData()
          SweetAlertConnector.closeCurrentAlert();
          SweetAlertConnector.showSuccessAlert('Invoice data read successfully.');
          this.calculateTotalCargoValue()
      })
      .catch(error => {
        console.error('Error:', error);
        this.invoiceDataArray = [];
      });
    },
    calculateTotalExtensionPrice(items) {
      return items.reduce((acc, item) => acc + this.parseFloatFromString(item.amount || 0), 0).toFixed(2);
    },
    updateData(data, field, value) {
      data[field] = value;
    },
    updateItem(data, index, field, value) {
      data.invoice_items[index][field] = value;
    },
    deleteRow(data, index) {
      data.invoice_items.splice(index, 1);
    },
    addNewLine(data) {
      const newItem = {
        product_code: '',
        description: '',
        thn_code: '',
        quantity: '',
        unit_price: '',
        amount: ''
      };
      data.invoice_items.push(newItem);
    },
    extensionStyle(item) {
      const expected = (parseFloat(item.unit_price) * parseFloat(item.quantity)).toFixed(2);
      const actual = parseFloat(item.amount).toFixed(2);
      if ([item.unit_price, item.quantity, item.amount].some(v => [null, undefined, '', false].includes(v))) {
        return { backgroundColor: 'yellow', color: 'black' };
      } else if (expected !== actual) {
        return { backgroundColor: 'red', color: 'white' };
      }
      return {};
    },
    naStyle(value) {
      if ([null, undefined, '', false].includes(value)) {
        return { backgroundColor: 'yellow', color: 'black' };
      }
      return {};
    },
    checkInvoiceTotal(data) {
      const totalExtensions = this.calculateTotalExtensionPrice(data.invoice_items);
      console.log(data)
      console.log(totalExtensions, data.invoice_total)
      const invoiceTotal = parseFloat(data.invoice_total || 0).toFixed(2);
      return totalExtensions !== invoiceTotal ? { backgroundColor: 'red', color: 'white' } : {};
    },
    parseFloatFromString(inputString) {

        if (!inputString || inputString === undefined || inputString === null) {
            return 0;
        }

        if(typeof inputString === 'number') {
            return inputString;
        }
        // Remove non-numeric characters except for period (.)
        let numericString = inputString.replace(/[^0-9.]/g, '');

        // Parse the numeric string as a float
        let floatValue = parseFloat(numericString);

        return floatValue;
    },
    exportToXML() {
      let xml = '<?xml version="1.0" encoding="UTF-8"?>\n';
      xml += '<ASYCUDA>\n';

      this.invoiceDataArray.forEach((invoice, index) => {
        xml += `  <Invoice index="${index}">\n`;
        invoice.invoice_items.forEach((item, itemIndex) => {
          xml += `    <Item index="${itemIndex}">\n`;
          xml += `      <ProductCode>${item.product_code}</ProductCode>\n`;
          xml += `      <Description>${item.description}</Description>\n`;
          xml += `      <THNCode>${item.thn_code}</THNCode>\n`;
          xml += `      <Quantity>${item.quantity}</Quantity>\n`;
          xml += `      <UnitPrice>${item.unit_price}</UnitPrice>\n`;
          xml += `      <ExtensionPrice>${item.amount}</ExtensionPrice>\n`;
          xml += '    </Item>\n';
        });
        xml += '  </Invoice>\n';
      });

      xml += '</ASYCUDA>\n';

      const blob = new Blob([xml], { type: 'application/xml' });
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = 'invoices.xml';
      link.click();
      URL.revokeObjectURL(link.href);
    }
  }
}
</script>


<style>
